<template>
    <ul>
        <li>
            <p>切换年月：
                <!-- <da-radio-select v-model="contaminant" :maxNum="5" :option-lists="contaminants" label="name" value-key="id" @change="changeCont"></da-radio-select> -->
                <da-time-select v-model="contaminant" :show-time="[1,0]" @inputChange="changeTime" @radioChange="changeCont"></da-time-select>
            </p>
            <da-calendar ref="calendar" :target-id="targetId" :disabled="!targetId" v-model="calendarTime" @change="changeData" excess-attr="excess">
                <template slot="content" slot-scope="scope">
                    <p class="text-center">产量：{{scope.data?scope.data.cl:"-"}}</p>
                    <p class="text-center">能耗：{{scope.data?scope.data.nh:"-"}}</p>
                    <p class="text-center">单耗：{{scope.data?scope.data.dh:"-"}}</p>
                </template>
            </da-calendar>
            <code>contaminant:{{contaminant}}，calendarTime:{{calendarTime}}
                <p>使日期变色属性excessAttr，日历的属性targetId:{targetCycle:3（显示的时间类型日3/月5/年8）,id:12（后台的指标数据管理用到）}</p>
                <p>日期下显示其他内容方式：增加带 slot="content" 标签， 获取每组数据时 slot-scope="scope" ,拥有date（时间）和data（当前时间的数据[在调用this.$refs.calendar.setInputs(calendarData, "cl", "time")时设值并自动对应时间]）两个对象值 </p>
            </code>
        </li>
    </ul>
</template>

<script>
import daCalendar from "@/components/base/calendar.vue";
export default {
  components: {
    daCalendar
  },
  data() {
    return {
      contaminant: "2018-5",
      contaminants: [{ name: "年", id: 5 }, { name: "月", id: 3 }],
      // 日历
      targetId: { targetCycle: 3 },
      calendarTime: []
    };
  },
  methods: {
    //   模拟数据
    changeData() {
      let calendarData = [];
      switch (this.targetId.targetCycle) {
        case 3:
          this.$each(new Array(30), (item, index) => {
            calendarData.push({
              cl: (Math.random() * 100).toFixed(2),
              nh: (Math.random() * 100).toFixed(2),
              dh: (Math.random() * 100).toFixed(2),
              time: "2018-08-" + (index + 1),
              excess: Math.random() > 0.5
            });
          });
          break;
        case 5:
          this.$each(new Array(12), (item, index) => {
            calendarData.push({
              cl: (Math.random() * 100).toFixed(2),
              nh: (Math.random() * 100).toFixed(2),
              dh: (Math.random() * 100).toFixed(2),
              time: "2018-" + (index + 1) + "-01",
              excess: Math.random() > 0.5
            });
          });
          break;
      }
      this.$refs.calendar.setInputs(calendarData, "cl", "time");
    },
    changeTime(date, type) {
      let time = this.contaminant.split("-");
      this.$refs.calendar.render(time[0], parseInt(time[1]) - 1);
      this.changeData();
    },
    changeCont(date, type) {
      let obj = { 5: 3, 8: 5 };
      this.targetId.targetCycle = obj[type];
      this.changeData();
    }
  },
  mounted() {
    this.changeData();
  }
};
</script>



