<template>
  <div>
    <h3>布局</h3>
    <layout></layout>
    <h3>组件</h3>
    <ul>
      <li>
        <p>时间选择</p>
        <da-time v-model="dataTime" type="daterange" placeholder="选择日期"></da-time>
        <code>dataTime:{{dataTime}}
          <p>type:year/month/date/week/datetime 、datetimerange/daterange</p>
        </code>
        <da-time-select v-model="time" :time-type.sync="timeType0" :show-time="[3,5,8]" @change="changeTime" @radioChange="(date,type)=>{ }" @inputChange="(date,type)=>{}"></da-time-select>
        <small>说明：show-time为显示年月日及其顺序，2为日、1为月、0为年 {{timeType0}}</small>
        <code>time:{{time}}--timeType0：{{timeType0}}---
          <el-button @click="timeType0=5">修改类型为月</el-button>
          <el-button @click="time='2019-05-03'">修改时间</el-button>
        </code>
        <p>
          <da-time-range v-model="time2" :time-type.sync="timeType" :show-time="[3,5,8]" :label="{sLabel:'开始',eLabel:'结束'}" :isPickerEnd="isPickerEnd" @change="changeTime2"></da-time-range>
          <code>time2:{{time2}}---timeType:{{timeType}}
            <el-button @click="time2=['2018-01-01','2018-05-03']">动态修改时间</el-button>
            <el-button @click="isPickerEnd=!isPickerEnd">是否限制第二个时间--isPickerEnd:{{isPickerEnd}}</el-button>
            <p>:label="{sLabel:'开始',eLable:'结束'}"</p>
          </code>
        </p>
        <p>
          <da-cus-time v-model="timeSpan" ref="cusTime" :time-type.sync="timeCycle" @change="cusTimeHandle"></da-cus-time>
          <code>timeSpan:{{timeSpan}}---timeCycle:{{timeCycle}}
            <p>可自定义raido[timeRadio]----
            </p>
          </code>
        </p>
      </li>
      <li>
        <p>radio/select</p>
        <p>
          <da-radio-select v-model="contaminant" :maxNum="5" :option-lists="contaminants" label="name" value-key="id" @change="changeCont"></da-radio-select>
          <code>contaminant:{{contaminant}}</code>
        </p>
        <small>当option-lists的长度大于maxNum时,radio会自动变为select,如下：</small>
        <p>
          <da-radio-select v-model="contaminant2" :maxNum="3" :option-lists="contaminants" label="name" value-key="id" @change="changeCont"></da-radio-select>
          <code>contaminant2:{{contaminant2}}
            <p>contaminants.length小于maxNum，显示radio</p>
          </code>
        </p>
      </li>
      <li>
        <p>
          <da-radio-block v-model="radioBlock" :data-lists="contaminants" label="name" value-key="id" @change="radioChange"></da-radio-block>
          <code>radioBlock:{{radioBlock}}</code>
        </p>
      </li>
      <li>
        <p>树形结构{{treeValue}}</p>
        <da-tree v-model="treeValue" :load="true" :data-lists="treeData" :default-expand="true" :show-checkbox="true" @currentChange="treeChange"></da-tree>
        <code>treeValue:{{treeValue}}
          <p>currentChange选中事件</p>
          <p>showCheckbox（Boolean,是否显示复选框)），expandOnClickNode（Boolean,是否点击展开或关闭节点），checkStrictly（Boolean,父子是否不关联做法），defaultKeys（哪些选项默认展开及默认选中）</p>
        </code>
      </li>
    </ul>
    <h3>图形</h3>
    <ul>
      <li>
        <p>不传时间dateTime：</p>
        <da-line-chart id="noTimelineChart" type="line" :data-lists="lineChartData" x-axis-key="date" value-key="value" x-axis-format="YYYY-MM"></da-line-chart>
        <p>普通的折线/柱形图</p>
        <da-line-chart id="TimelineChart" type="line" :date-time="time" :data-lists="lineChartData" format="YYYY" x-axis-key="date" value-key="value" x-axis-format="YYYY-MM"></da-line-chart>
        <p>同比环比：</p>
        <da-line-chart id="lineChart" :date-time="time" :data-lists="lineChartData" title="同比环比" label="date" x-axis-key="date" value-key="value" format="YYYY" x-axis-format="MM月" legend-format="YYYY年"></da-line-chart>
        <code>
          time:{{time}}
          <ul>
            <li>
              <p>折线图与柱形图的转换：type="line/bar"</p>
            </li>
            <li>
              <p>dateTime [Array,String]:传入数组为自定义x轴，传单个时间（如"2018-08"）表示八月的1号到31号，传两个时间（如"2018-08-01,2018-08-13"）表示八月的1号到13号</p>
            </li>
            <li>
              <p>折线图/柱形图：x为时间轴,多y轴nyAxis(Number；Array:20180919多y轴情况下可控制显示在哪条轴上,如[2,0,1]表示生成的series中第一条yAxisIndex:2,第二条为0)、是否支持区域缩放hasDataZoom、 </p>
            </li>
            <li>
              <p>格式化时间format(dateTime属性为字符串时，自动计算x轴并且格式化[年会自动格式化为月，往下推])，x轴时间显示格式化xAxisFormat,legend的时间显示格式化legendFormat（常用于同比环比，相同的指标不一样的时间节点）
              </p>
            </li>
            <li>
              <p>20180831增加标杆值、定额值、目标值计算(lineData)、传入请求targetData/bechemark的返回值{taget:[],banchmark:[],quota:[]}
              </p>
              <small>&nbsp;&nbsp;&nbsp;注意lineData必须要在dataLists之前赋值才能显示</small>
              <p>20180904增加自定义标杆定额目标值顺序及自定义属性lineDataAttr:[ {name: "定额值", yIndex: 0, attr: "quota", color: "#c23531"}...]
              </p>
              <p>20180919增加单独修改定额标杆目标值的函数,调用方法：this.$refs.XXXChart.changeTarget({taget:[],banchmark:[],quota:[]});
              </p>
              <p>
                <strong>【标杆定额目标值可查看例子：
                  <a class="link-a" @click="toRoute('comprehensiveTargetAnalysis')">综合能效对标分析</a>】
                </strong>
              </p>
            </li>
          </ul>
        </code>
      </li>
      <li>
        <p>仪表盘</p>
        <da-gauge-chart id="gaugeChart" title="碳排放量" width="400px" height="210px" :date-time="time" :data-lists="reduceChartData"></da-gauge-chart>
      </li>
      <li>
        <p>饼图</p>
        <da-pie-chart id="pieChart" title="污染物占比" :data-lists="pieData" value-key="value"></da-pie-chart>
      </li>
    </ul>
    <h3>数组展示</h3>
    <ul>
      <li>
        <p>告警（mode:horizontal / vertical）</p>
        <alarm-data :dataLists="contaminants" label="name" value-key="id" mode="horizontal"></alarm-data>
        <alarm-data :dataLists="contaminants" label="name" value-key="id" mode="vertical"></alarm-data>
      </li>
      <li>
        <p>色块</p>
        <div>
          <carbon-data :data-lists="carbonData" width="25%" bgColor="#fff">
            <p slot="add" slot-scope="scope">{{scope}}</p>
            <!-- <div slot-scope="scope">{{scope.data}}</div> -->
          </carbon-data>
        </div>
      </li>
    </ul>
    <h4>表格</h4>
    <ul>
      <li>
        <p>分页表格,table-list传入对象</p>
        <da-table class="analysis-table" :colums="columnData" :table-list="dataLists" @callBack="getData"></da-table>
      </li>
      <li>
        <p>不分页表格，table-list传数组</p>
        <da-table class="analysis-table" :colums="columnData" :table-list="dataLists.records"></da-table>
      </li>
    </ul>
    <h4>日历</h4>
    <da-calendar></da-calendar>
  </div>
</template>

<script>
import alarmData from "../template/alarmData";
import daCusTime from "../template/customTimeSelect";
import daCalendar from "./example/calendar"; // 日历
import layout from "./example/layout"; // 布局
import carbonData from "./components/carbonData";
export default {
  components: { layout, alarmData, daCusTime, daCalendar, carbonData },
  data() {
    return {
      //组件
      dataTime: ["2018-01-01", "2018-10-01"],
      time: "",
      timeType0: 8,
      time2: [],
      timeType: 5,
      isPickerEnd: false,
      timeSpan: ["2018-05-04", "201-06-01"],
      timeCycle: 5,
      treeValue: "",
      contaminant: "",
      contaminant2: "",
      contaminants: [],
      treeData: [],
      radioBlock: "",
      carbonData: [],
      //   图表数据
      lineChartData: [],
      reduceChartData: {},
      pieData: [],
      // 表格
      columnData: [
        {
          label: "对比项目",
          prop: "name"
        },
        {
          label: "数值",
          prop: "value"
        }
      ],
      dataLists: {}
    };
  },
  methods: {
    changeTime(date, type) {
      //   时间切换回调函数
      console.log(date, type);
      this.changeChartData();
    },
    changeTime2(date, type) {
      //   时间切换回调函数
      console.log(date, type);
    },
    cusTimeHandle() {},
    changeCont(value) {
      console.log(value);
    },
    changeCont2(value) {
      console.log(value);
    },
    treeChange(node) {
      // 叶子节点
      if (!node.obj.children) {
        console.log(node);
      }
    },
    radioChange(data) {
      console.log(data);
    },
    // 模拟数据集合
    getData() {
      let name = ["二氧化碳", "二氧化硫", "氮氧化合物", "烟尘"];
      let word = ["CO₂", "SO₂", "NOX", "Smoke"];
      let data = [];
      this.$each(name, (item, index) => {
        // radio
        data.push({
          name: item,
          id: index
        });
      });
      this.contaminants = data;

      let tree = [
        {
          name: "云舜",
          id: 0,
          children: []
        }
      ];
      this.$each(name, (item, index) => {
        tree[0].children.push({
          name: item,
          id: index,
          children: [
            {
              name: word[index],
              id: 10 + index
            }
          ]
        });
      });
      this.treeData = tree;

      this.dataLists = {
        aspnetPager: {
          currentPage: 1,
          startIndex: 0,
          endIndex: 10,
          pageSize: 10,
          totalCount: 4,
          totalPage: 0
        },
        records: [
          { name: "2017年8月", value: "156635.36 kWh" },
          { name: "2016年8月", value: "156635.36 kWh" },
          { name: "差值", value: "5516.24" }
        ]
      };
      setTimeout(() => {
        this.changeChartData();
      }, 200);
    },
    getReduceData() {
      let name = ["二氧化碳", "二氧化硫", "", "烟尘"];
      let word = ["CO₂", "SO₂", "NOX", "Smoke"];
      let data = [];
      this.$each(name, (item, index) => {
        data.push({
          name: item,
          word: word[index],
          data: (Math.random() * 300).toFixed(2),
          ratio: (5 - Math.random() * 10).toFixed(2),
          unit: "吨"
        });
      });
      this.carbonData = data;
    },
    changeChartData() {
      let names = ["二氧化碳", "二氧化硫", "氮氧化合物", "烟尘"];
      let word = ["CO₂", "SO₂", "NOX", "Smoke"];
      let curName = names[this.contaminant];
      let line = [];
      this.$each(new Array(parseInt(Math.random() * 12)), (item, index) => {
        if (10 - Math.random() * 100 < 0) {
          line.push({
            date: new Date().getTime() + (index - 7) * 1000 * 60 * 60 * 24 * 30, //"2018" + "-0" + (index + 1) + "-01",
            name: curName,
            value: parseInt(Math.random() * 100)
          });
          line.push({
            date:
              new Date().getTime() +
              (index - 7) * 1000 * 60 * 60 * 24 * 30 -
              1000 * 60 * 60 * 24 * 30 * 12, //"2017" + "-0" + (index + 1) + "-01",
            name: curName,
            value: parseInt(Math.random() * 100)
          });
        }
      });
      this.lineChartData = line;
      this.reduceChartData = {
        min: 0,
        max: parseInt(1000 + Math.random() * 1000),
        value: Math.random() * 1000,
        unit: "吨(t)"
      };
      let pie = [];
      this.$each(names, item => {
        pie.push({
          name: item,
          value: (Math.random() * 100).toFixed(2)
        });
      });
      this.pieData = pie;
    },
    changeHandle() {},
    // 路由跳转
    toRoute(path) {
      this.$router.push({ path });
    }
  },
  mounted() {
    this.getData();
    this.getReduceData();
    this.timeType = 8;
  }
};
</script>

<style>
code {
  display: block;
  border: 1px solid #ddd;
  background-color: #f5f5f5;
  padding: 15px;
  margin: 10px 0;
  line-height: 1.5em;
}
code ul {
  list-style: square;
  padding-left: 20px;
}
</style>




